export interface ListProps {
  list: string[];
  type: "ol" | "ul";
  style?: string;
  icon?: React.ReactNode;
}

export const Bullets = ({ type, list, style, icon }: ListProps) => {
  const renderList = () => {
    if (type === "ol") {
      return (
        <ol className={style}>
          {list.map((text, index) => (
            <li className="flex flex-row space-x-2" key={text}>
              <div>{index + 1}.</div>
              <div>{text}</div>
            </li>
          ))}
        </ol>
      );
    } else {
      return (
        <ul className={style}>
          {list.map((text, index) => (
            <li className="flex flex-row space-x-2" key={index}>
              {icon ? <div>{icon}</div> : <div>-</div>}
              <div>{text}</div>
            </li>
          ))}
        </ul>
      );
    }
  };

  return renderList();
};
